body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

button:hover {
  cursor: pointer;
}

button:focus {
  outline: 0;
}

.app {
  margin: 0 auto;
  width: 80%;
}

.navigation {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin: 10px 0;
  padding: 0;
}

.navigation li {
  list-style: none;
  display: block;
}

.navigation li button {
  border: 1px solid #999;
  color: #444;
  padding: 10px;
  text-transform: uppercase;
  display: block;
  width: 100%;
  height: 60px;
}

.chordsTitle,
.chordsSubtitle {
  text-align: center;
}

#chords {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.chord {
  background: #1843a7;
  border: 1px solid #0e0606;
  padding: 5px;
  width: 210px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  font-weight: bold;
  color: #fff;
  border-radius: 5px;
  box-shadow: 9px 11px 21px -7px rgba(38, 38, 38, 1);
}

.chord:nth-child(odd) {
  background: #51a91f;
  border: 1px solid #2e7406;
}

.chord:hover {
  cursor: pointer;
}

.timer__time {
  text-align: center;
  font-size: 2em;
  padding: 10px;
}
